.project {
    max-width: 370px;
    background-color: var(--project-card-bg);
    border-radius: 10px;
    box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.project__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 1.3;
    padding: 15px 20px 25px;
    color: var(--project-card-text);
}

.project__img {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}